.gallery {
  overflow: auto;
  box-shadow: 1px 2px 2px #a0a0a0;
  background-color: #f0f0f0;
}

h2 {
  text-align: center;
  margin: 2rem 1rem 1rem;
  padding: 0.5rem 2rem;

  font-size: 1rem;
  background-color: #00a1b9;
  box-shadow: 4px 4px #01546c;
}

.item-container {
  margin: 1rem 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

@media (min-width: 250px) {
  h2 {
    font-size: 1.5rem;
  }
}

@media (min-width: 500px) {
  .gallery {
    margin: 1rem;
  }
  h2 {
    margin: 2rem 4% 1rem;
    text-align: left;
  }
}

@media (min-width: 780px) {
  h2 {
    margin: 2rem 2% 1rem;
  }
}
