.item {
  margin: 1rem 0;
  padding: 0.5rem 0;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;

  flex: 0 0 100%;
  border: 1px solid #c1c1c1;
  border-radius: 5px;
  background-color: #f5f5f5;
}

h3 {
  margin: 0.5rem 1rem;
  min-height: 3rem;

  font-size: 0.9rem;
  font-family: "Roboto", sans-serif;
  text-align: center;
  vertical-align: middle;
}

figure {
  margin: 0.5rem 2rem;
}

.item p {
  margin: 0.5rem 1rem;
  font-size: 0.9rem;
}
.item img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

@media (min-width: 250px) {
  h3 {
    font-size: 1.17rem;
  }
}

@media (min-width: 500px) {
  .item {
    margin-left: 4%;
    flex: 0 0 44%;
  }
}

@media (min-width: 780px) {
  .item {
    margin-left: 2%;
    flex: 0 0 22.5%;
  }
}

@media (min-width: 1200px) {
  .item {
    flex: 0 0 17.6%;
  }
}
