@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
/* Border box declaration */
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* end of common styles */

h1 {
  margin: 1rem 0;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  color: #00a1b9;

  text-shadow: 2px 2px 2px #01546c;
  background-color: #f0f0f0;
  box-shadow: 1px 2px 2px #a0a0a0;
}

@media (min-width: 250px) {
  h1 {
    font-size: 3rem;
  }
}

@media (min-width: 500px) {
  h1 {
    font-size: 3.5rem;
    margin: 1rem;
  }
}

.item {
  margin: 1rem 0;
  padding: 0.5rem 0;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;

  flex: 0 0 100%;
  border: 1px solid #c1c1c1;
  border-radius: 5px;
  background-color: #f5f5f5;
}

h3 {
  margin: 0.5rem 1rem;
  min-height: 3rem;

  font-size: 0.9rem;
  font-family: "Roboto", sans-serif;
  text-align: center;
  vertical-align: middle;
}

figure {
  margin: 0.5rem 2rem;
}

.item p {
  margin: 0.5rem 1rem;
  font-size: 0.9rem;
}
.item img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

@media (min-width: 250px) {
  h3 {
    font-size: 1.17rem;
  }
}

@media (min-width: 500px) {
  .item {
    margin-left: 4%;
    flex: 0 0 44%;
  }
}

@media (min-width: 780px) {
  .item {
    margin-left: 2%;
    flex: 0 0 22.5%;
  }
}

@media (min-width: 1200px) {
  .item {
    flex: 0 0 17.6%;
  }
}

.gallery {
  overflow: auto;
  box-shadow: 1px 2px 2px #a0a0a0;
  background-color: #f0f0f0;
}

h2 {
  text-align: center;
  margin: 2rem 1rem 1rem;
  padding: 0.5rem 2rem;

  font-size: 1rem;
  background-color: #00a1b9;
  box-shadow: 4px 4px #01546c;
}

.item-container {
  margin: 1rem 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

@media (min-width: 250px) {
  h2 {
    font-size: 1.5rem;
  }
}

@media (min-width: 500px) {
  .gallery {
    margin: 1rem;
  }
  h2 {
    margin: 2rem 4% 1rem;
    text-align: left;
  }
}

@media (min-width: 780px) {
  h2 {
    margin: 2rem 2% 1rem;
  }
}

