h1 {
  margin: 1rem 0;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  color: #00a1b9;

  text-shadow: 2px 2px 2px #01546c;
  background-color: #f0f0f0;
  box-shadow: 1px 2px 2px #a0a0a0;
}

@media (min-width: 250px) {
  h1 {
    font-size: 3rem;
  }
}

@media (min-width: 500px) {
  h1 {
    font-size: 3.5rem;
    margin: 1rem;
  }
}
